@import url('https://fonts.googleapis.com/css2?family=Rubik+Bubbles&display=swap');
.container{
    .header{
        display: flex;
        align-items: center;
        justify-content: space-around;
        .logo{
            margin-top: 2rem;
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 1rem;
            font-family: 'Rubik Bubbles', cursive;
            font-size: 2rem;
            margin-bottom: 2rem;
            img{
                width: 2rem;
            }
    }
    }
    .social{
        z-index: inherit;
        display: flex;
        gap: 1rem;
        a>*{
            scale: 2;
            margin-left: 2rem;
            &:hover{
                scale: 2.2;
            }
        }
        }
}

@media screen and (max-width:1024px) {
    .header{
        width: 100%;
    }
    .header>*{
        scale: 2;
    }
    .social{
        margin-left: 16rem;
    }
}
@media screen and (max-width:600px) {
    .header>*{
        scale: 2;
    }
    .social{
        margin-left: 8rem;
    }

}